import React, {useEffect, useState} from 'react';
import Header from "../../components/UI/Header/Header";
import {useDispatch, useSelector} from "react-redux";
import {Navigate} from "react-router-dom";
import {setSpecialtiesList} from "../../features/specialties/specialitiesSlice";
import {FormControl, InputLabel, MenuItem, Typography} from "@mui/material";
import Select from '@mui/material/Select';
import classes from './UserSpecialities.module.css'
import createCache from '@emotion/cache';
import {CacheProvider} from "@emotion/react";

const cache = createCache({
    key: 'css',
    prepend: true,
});

const UserSpecialities = () => {
    const auth = useSelector((state) => state.user.isAuth)
    const dispatch = useDispatch()
    useEffect(() => {
        dispatch(setSpecialtiesList())
    }, [dispatch])
    // const specialitiesList = useSelector((state) => state.specialities.specialitiesList)
    const [form, setForm] = useState('')
    const [time, setTime] = useState('')
    const [payment, setPayment] = useState('')
    const handleFormStateChange = (e) => {
        setForm(e)
        if (e === '') {
            setTime('')
            setPayment('')
        }
    }

    if (!auth) return <Navigate to="/"/>


    return (
        <CacheProvider value={cache}>
        <div>
            <Header page="applic"/>
            {/*    next element should be with marginTop: "100px" because of the positioning of the header element*/}
            <div className={classes.Configuration}>
                <Typography variant="h3" className={classes.ConfigurationName}>  </Typography>
                <div className={classes.SelectorWrap}>
                    <FormControl className={classes.ConfigurationFormControl}>
                        <InputLabel id="form-select-label"> </InputLabel>
                        <Select
                            labelId="form-select-label"
                            id="form-select"
                            value={form}
                            onChange={e => handleFormStateChange(e.target.value)}
                            label=" "
                        >
                            <MenuItem value={''}></MenuItem>
                            <MenuItem value={''}></MenuItem>
                            <MenuItem value={''}></MenuItem>
                        </Select>
                    </FormControl>
                    <FormControl disabled={form === ''} className={classes.ConfigurationFormControl}>
                        <InputLabel id="time-select-label"> </InputLabel>
                        <Select
                            labelId="time-select-label"
                            id="time-select"
                            value={time}
                            onChange={e => setTime(e.target.value)}
                            label=" "
                        >
                            <MenuItem value={''}></MenuItem>
                            <MenuItem value={''}></MenuItem>
                        </Select>
                    </FormControl>
                    <FormControl disabled={form === ''} className={classes.ConfigurationFormControl}>
                        <InputLabel id="payment-select-label"> </InputLabel>
                        <Select
                            labelId="payment-select-label"
                            id="payment-select"
                            value={payment}
                            onChange={e => setPayment(e.target.value)}
                            label=" "
                        >
                            <MenuItem value={''}></MenuItem>
                            <MenuItem value={''}></MenuItem>
                        </Select>
                    </FormControl>
                </div>
            </div>
        </div>
        </CacheProvider>
    );
};

export default UserSpecialities;